.app {
  min-height: 300vh;
}

.start {
  position: fixed;
  width: 32px;
  height: 32px;
  top: 50px;
  right: 250px;
  background: #ef9901;
}

.end {
  display: inline-block;
  margin-left: 800px;
  margin-top: 500px;
  width: 32px;
  height: 32px;
  top: 30px;
  right: 100px;
  background: #ef9901;
}

.point {
  position: fixed;
  top: 80px;
  left: 40px;

  cursor: pointer;

  transition: transform .5s cubic-bezier(0.02, 0.01, 0.21, 1);

  &::after {
    content: '';
    border-radius: 100%;
    width: 12px;
    height: 12px;
    display: inline-block;
    background: rgb(233, 125, 125);
    transition: transform .5s cubic-bezier(0.3, 0.27, 0.07, 1.64);
  }
}

.point-end {
  transform: translateX(100px);
  &::after {
    transform: translateY(400px);
  }
}